// src/pages/Profile.jsx
import React from 'react';
import {
  Box,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  ListItemButton,
  Avatar,
  Typography,
  Divider,
  Paper,
} from '@mui/material';
import {
  PhotoCamera,
  Wallet,
  Settings,
} from '@mui/icons-material';
import AppHeader from '../components/AppHeader';
import TabNavigation from '../components/TabNavigation';

const Profile = () => {
  const menuItems = [
    {
      icon: <PhotoCamera color="primary" />,
      title: '',
      subtitle: '100',
    },
    {
      icon: <Wallet color="primary" />,
      title: '',
      subtitle: '',
    },
    {
      icon: <Settings color="primary" />,
      title: '',
      subtitle: '',
    },
  ];

  return (
    <Box sx={{ pb: 7 }}>
      <AppHeader title="" />
      
      <Paper sx={{ mt: 7, mb: 2 }}>
        <ListItem sx={{ py: 2 }}>
          <ListItemAvatar>
            <Avatar
              sx={{ width: 64, height: 64 }}
              src="/assets/images/avatar.jpg"
              alt=""
            />
          </ListItemAvatar>
          <ListItemText
            primary={
              <Typography variant="h6" sx={{ ml: 2 }}>
                
              </Typography>
            }
            secondary={
              <Typography variant="body2" color="text.secondary" sx={{ ml: 2 }}>
                : myusername
              </Typography>
            }
          />
        </ListItem>
      </Paper>

      <List>
        {menuItems.map((item, index) => (
          <React.Fragment key={index}>
            <ListItem disablePadding>
              <ListItemButton>
                <ListItemAvatar>
                  {item.icon}
                </ListItemAvatar>
                <ListItemText
                  primary={item.title}
                  secondary={item.subtitle}
                />
              </ListItemButton>
            </ListItem>
            {index < menuItems.length - 1 && <Divider />}
          </React.Fragment>
        ))}
      </List>
      
      <TabNavigation />
    </Box>
  );
};

export default Profile;